/*
 * Signify that something is an Object, and that it may be used in any number of unrelated contexts to the one you can currently see it in.
 * Making modifications to these types of class could potentially have knock-on effects in a lot of other unrelated places.
 *
 * Tread carefully.
 */

$pattern-circle-size: 0.8px
$pattern-circle-distance-relative: 25

$content-margin: 5%
$header-footer-padding: 1%

.o-header
  width: 100%
  // Pattern Dot Background
  background-image: radial-gradient($color-header-fg, $pattern-circle-size, transparent $pattern-circle-size)
  background-size: calc(#{$pattern-circle-distance-relative} * #{$pattern-circle-size}) calc(#{$pattern-circle-distance-relative} * #{$pattern-circle-size})
  background-repeat: repeat

  .o-header__header
    padding: 2rem
    display: flex
    justify-content: space-between

    @include breakpoint(xxs)
      flex-wrap: wrap
      justify-content: space-evenly

    @include breakpoint(md)
      padding: $header-footer-padding

  .o-header__h1
    margin-left: $content-margin
    box-sizing: border-box

  .o-header__fade
    width: 100%
    height: 100px
    background: $color-header-bg
    background: linear-gradient(180deg, rgba(255,255,255,0.2) 0%, rgba(255,255,255,1) 100%)

.o-logo
  height: 55px
  background-color: #fff

.o-h1-header
  .o-h1-header__h1
    word-break: break-word
    hyphens: auto

    @include fontRobotoBoldItalic
    margin-bottom: 1rem
    font-size: 3rem

    @include breakpoint(xxs)
      margin: 0 0 1.5rem 0
      font-size: 2.5rem

    @include breakpoint(sm)
      font-size: 3.5rem

    @include breakpoint(md)
      font-size: 4rem

  .o-h1-header__description
    @include fontRoboto
    margin-top: 1rem
    background-color: $color-lt-background1
    display: inline
    padding: 3px
    line-height: 1.3 // needs adaption; otherwise the text would overlap itself

    @include breakpoint(xxs)
      font-size: 1.3rem

    @include breakpoint(md)
      font-size: 2rem

.o-h2
  margin: 20px 0 10px 0

  .o-h2__inner
    margin: 0
    @include fontRobotoBoldItalic
    font-size: $font-size-heading2 * $font-size-upto-md-scaling
    display: inline
    box-shadow: inset 0 -0.15em $color-lt-background1, inset 0 -0.4em $color-red
    padding: 0 5px // more space for the underline

    @include breakpoint(md)
      font-size: $font-size-heading2

  .o-h2--smaller
    font-size: $font-size-heading2 * 0.7

.o-h3
  @include fontRobotoBoldItalic
  font-size: $font-size-heading3 * $font-size-upto-md-scaling
  display: inline-block
  color: $color-font
  margin: 20px 0 10px 0

  @include breakpoint(md)
    font-size: $font-size-heading3

.o-link
  @include fontRoboto()
  font-size: $font-size-text
  color: $color-black
  text-decoration: underline
  transition: all 0.5s ease

  &:hover
    color: $color-font-perceptive2

.o-button
  border: none // as it as a submit button
  @include fontRoboto()
  font-size: $font-size-text
  text-decoration: none
  padding: 10px
  text-align: center
  display: inline-block
  background-color: $color-black
  color: $color-white
  transition: all 0.5s ease

  &:hover
    transform: scale(0.90)

.o-text
  @include fontRoboto()
  font-size: $font-size-text

.o-text--small
  font-size: $font-size-code
  margin: .5rem 0

.o-text--important
  background-color: $color-light-red
  padding: 5px

.o-quote
  display: flex
  flex-direction: column

  .o-quote__q
    text-align: center
    @include fontRobotoBoldItalic
    font-size: 2rem
    margin: 1.5rem 0

  .o-quote__author
    text-align: right



.o-list
  @include fontRoboto()
  font-size: $font-size-text

  .o-list__item
    margin-top: 10px

.o-content-wrapper
  display: flex
  justify-content: center
  padding: $content-margin
  width: 100%

  @include breakpoint(xs)
    flex-wrap: wrap
    justify-content: unset

  .o-content-wrapper__side
    @include breakpoint(sm)
      margin-right: 50px
    

  .o-content-wrapper__docs
    width: 100%

    @include breakpoint(sm)
      width: initial
      max-width: 70%

  .o-content-wrapper__content
    width: 100%

    @include breakpoint(lg)
      width: initial
      max-width: 1100px

  .o-content-wrapper__dashboard
    width: 100%
    height: 100%



.o-footer
  padding: 1% $content-margin
  display: flex
  justify-content: space-between
  margin-top: 4em

  @include breakpoint(xs)
    flex-direction: column-reverse

  @include breakpoint(sm)
    margin-top: 0

  .o-footer__created_by
    display: flex
    align-items: center
    @include fontRoboto

    @include breakpoint(xs)
      justify-content: center
      margin-bottom: 3rem

  .o-footer__logo
    height: 35px
    margin-right: 10px
    
  .o-footer__twitter
    background-color: $twitter-color
    color: $color-white
    padding: 4px
    text-decoration: none
    margin-left: 10px

  .o-footer__link-list
    list-style: none
    padding: 0

    @include breakpoint(xxs)
      flex-wrap: wrap

    @include breakpoint(xs)
      display: flex
      justify-content: center
      margin-bottom: 3rem

  .o-footer__link-elem
    float: left
    margin-right: 2rem

    @include breakpoint(xxs)
      margin-bottom: 15px

    @include breakpoint(xs)
      float: none

    &:last-of-type
      margin-right: 0

  .o-footer__link
    color: $color-font-perceptive1
    text-decoration: none
    font-size: $font-size-code

    &:hover
      color: $color-font-perceptive2

  .o-footer__link--big
    border: 1px $color-black solid
    border-radius: 5px
    padding: 4px
    

.o-vertical-menu
  .o-vertical-menu__link--active
    font-weight: bolder

.o-menu-list
  list-style: none
  padding: 0

  .o-menu-list__li
    float: left
    margin-right: 2rem

    @include breakpoint(xxs)
      margin-bottom: 15px

    @include breakpoint(xs)
      float: none

    &:last-of-type
      margin-right: 0

  .o-menu-list__link
    color: $color-font-perceptive1
    text-decoration: none
    font-size: $font-size-code
    background-color: $color-white

    &:hover
      color: $color-font-perceptive2

  .o-menu-list__link--big
    border: 1px $color-black solid
    border-radius: 5px
    padding: 4px



.o-mail-dialog
  width: 100%
  display: flex
  margin-top: 2em
  flex-direction: column

  @include breakpoint(sm)
    flex-direction: row

  .o-mail-dialog__why
    list-style: none
    width: 100%
    padding: 0
    margin: 0 0 2em 0

    @include breakpoint(sm)
      width: 50%
      margin: 0
      padding: 0 20px 0 0

  .o-mail-dialog__why-li
    @include fontRoboto
    font-size: $font-size-text
    display: flex
    align-items: center
    margin-bottom: 1em

    @include breakpoint(sm)
      margin-bottom: 2em

    &:before
      box-sizing: content-box
      content: ''
      display: inline-block
      height: 1.5em
      width: 1.5em
      background-image: url('/img/check-icon.svg')
      background-size: contain
      background-repeat: no-repeat
      padding-left: 1em

  .o-mail-dialog__input-container
    width: 100%

    @include breakpoint(sm)
      width: 50%
      height: 100%

.o-unified-button-input
  width: 100%
  display: flex
  flex-direction: column

  @include breakpoint(sm)
    flex-direction: row

  .o-unified-button-input__input
    @include fontRoboto
    font-size: $font-size-code
    box-sizing: border-box
    border: 1px solid $color-black
    width: 100%
    margin-bottom: 1em
    padding: $padding-dashboard-li
    outline: none

    @include breakpoint(sm)
      margin-bottom: 0
      width: 60%

  .o-unified-button-input__button
    @include fontRoboto
    font-size: $font-size-code
    text-align: center
    border: none
    padding: $padding-dashboard-li
    display: block
    width: 100%
    background-color: $color-black
    color: $color-white
    transition: background-color 0.5s ease

    @include breakpoint(sm)
      width: 40%

    &:hover
      background: $color-black2

.o-single-submit-form
  .o-single-submit-form__input
    @include fontRoboto()
    font-size: $font-size-text
    border: 2px #000 solid
    width: 100%
    margin: 0
    padding: 5px
    outline: none

    &:focus
      box-shadow: 0 0 3px 0 #000

  .o-single-submit-form__agree-text
    margin: 1em 0
    @include fontRoboto()
    color: $color-black4
    font-size: $font-size-code

    &:last-of-type
      margin: 1rem 0 2.5rem 0

  .o-single-submit-form__agree-links, .o-single-submit-form__agree-links:visited
    color: $color-black4

  .o-single-submit-form__button
    border: none // as it as a submit button
    @include fontRoboto()
    font-size: $font-size-text
    text-decoration: none
    padding: 10px
    text-align: center
    display: block
    width: 100%
    background-color: $color-black
    color: $color-white
    transition: all 0.5s ease

    &:hover
      transform: scale(1.05)


/*
 * Used whenever something needs to be completely centered
 */
.o-center-wrapper
  display: flex
  justify-content: center
  align-items: center
  height: 100%


/*
 * Used for App Dashboard
 */

.o-table
  width: 100%
  margin: ($padding-dashboard-li * 4) 0 0 0

  .o-table__th
    @include fontRobotoBoldItalic
    font-size: $font-size-text
    padding: $padding-dashboard-li
    border: solid $color-black
    border-width: 0 1px 1px 0
    margin: 0

    &:last-of-type
      border-width: 0 0 1px 0

  .o-table__td
    @include fontRoboto()
    font-size: $font-size-text
    padding: $padding-dashboard-li
    border: solid $color-black
    border-width: 0 1px 0 0
    margin: 0

    &:last-of-type
      border-width: 0

.o-pure-text-wrap
  white-space: pre-line

  p
    @include fontRoboto()
    font-size: $font-size-text
    margin: .25em 0

  ul,ol
    white-space: normal
    margin: 0