html {
  background-color: #fff;
  height: 100%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  height: 100%;
}

pre {
  padding: 15px;
  overflow-x: scroll;
}

/* * Signify that something is an Object, and that it may be used in any number of unrelated contexts to the one you can currently see it in.
 * * Making modifications to these types of class could potentially have knock-on effects in a lot of other unrelated places.
 * *
 * * Tread carefully.
 * */
.o-header {
  width: 100%;
  background-image: radial-gradient(#000, 0.8px, transparent 0.8px);
  background-size: calc(25 * 0.8px) calc(25 * 0.8px);
  background-repeat: repeat;
}
.o-header .o-header__header {
  padding: 2rem;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 500px) {
  .o-header .o-header__header {
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
}
@media (min-width: 992px) {
  .o-header .o-header__header {
    padding: 1%;
  }
}
.o-header .o-header__h1 {
  margin-left: 5%;
  box-sizing: border-box;
}
.o-header .o-header__fade {
  width: 100%;
  height: 100px;
  background: #fff;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%, rgb(255, 255, 255) 100%);
}

.o-logo {
  height: 55px;
  background-color: #fff;
}

.o-h1-header .o-h1-header__h1 {
  word-break: break-word;
  hyphens: auto;
  font-family: "robotobolditalic", sans-serif;
  margin-bottom: 1rem;
  font-size: 3rem;
}
@media (max-width: 500px) {
  .o-h1-header .o-h1-header__h1 {
    margin: 0 0 1.5rem 0;
    font-size: 2.5rem;
  }
}
@media (min-width: 768px) {
  .o-h1-header .o-h1-header__h1 {
    font-size: 3.5rem;
  }
}
@media (min-width: 992px) {
  .o-h1-header .o-h1-header__h1 {
    font-size: 4rem;
  }
}
.o-h1-header .o-h1-header__description {
  font-family: "robotoregular", sans-serif;
  margin-top: 1rem;
  background-color: #fff;
  display: inline;
  padding: 3px;
  line-height: 1.3;
}
@media (max-width: 500px) {
  .o-h1-header .o-h1-header__description {
    font-size: 1.3rem;
  }
}
@media (min-width: 992px) {
  .o-h1-header .o-h1-header__description {
    font-size: 2rem;
  }
}

.o-h2 {
  margin: 20px 0 10px 0;
}
.o-h2 .o-h2__inner {
  margin: 0;
  font-family: "robotobolditalic", sans-serif;
  font-size: 2.25rem;
  display: inline;
  box-shadow: inset 0 -0.15em #fff, inset 0 -0.4em #DC0334;
  padding: 0 5px;
}
@media (min-width: 992px) {
  .o-h2 .o-h2__inner {
    font-size: 3rem;
  }
}
.o-h2 .o-h2--smaller {
  font-size: 2.1rem;
}

.o-h3 {
  font-family: "robotobolditalic", sans-serif;
  font-size: 1.65rem;
  display: inline-block;
  color: #000;
  margin: 20px 0 10px 0;
}
@media (min-width: 992px) {
  .o-h3 {
    font-size: 2.2rem;
  }
}

.o-link {
  font-family: "robotoregular", sans-serif;
  font-size: 1.3rem;
  color: #000;
  text-decoration: underline;
  transition: all 0.5s ease;
}
.o-link:hover {
  color: #6e6e6e;
}

.o-button {
  border: none;
  font-family: "robotoregular", sans-serif;
  font-size: 1.3rem;
  text-decoration: none;
  padding: 10px;
  text-align: center;
  display: inline-block;
  background-color: #000;
  color: #fff;
  transition: all 0.5s ease;
}
.o-button:hover {
  transform: scale(0.9);
}

.o-text {
  font-family: "robotoregular", sans-serif;
  font-size: 1.3rem;
}

.o-text--small {
  font-size: 1rem;
  margin: 0.5rem 0;
}

.o-text--important {
  background-color: #d72d4f;
  padding: 5px;
}

.o-quote {
  display: flex;
  flex-direction: column;
}
.o-quote .o-quote__q {
  text-align: center;
  font-family: "robotobolditalic", sans-serif;
  font-size: 2rem;
  margin: 1.5rem 0;
}
.o-quote .o-quote__author {
  text-align: right;
}

.o-list {
  font-family: "robotoregular", sans-serif;
  font-size: 1.3rem;
}
.o-list .o-list__item {
  margin-top: 10px;
}

.o-content-wrapper {
  display: flex;
  justify-content: center;
  padding: 5%;
  width: 100%;
}
@media (max-width: 767px) {
  .o-content-wrapper {
    flex-wrap: wrap;
    justify-content: unset;
  }
}
@media (min-width: 768px) {
  .o-content-wrapper .o-content-wrapper__side {
    margin-right: 50px;
  }
}
.o-content-wrapper .o-content-wrapper__docs {
  width: 100%;
}
@media (min-width: 768px) {
  .o-content-wrapper .o-content-wrapper__docs {
    width: initial;
    max-width: 70%;
  }
}
.o-content-wrapper .o-content-wrapper__content {
  width: 100%;
}
@media (min-width: 1300px) {
  .o-content-wrapper .o-content-wrapper__content {
    width: initial;
    max-width: 1100px;
  }
}
.o-content-wrapper .o-content-wrapper__dashboard {
  width: 100%;
  height: 100%;
}

.o-footer {
  padding: 1% 5%;
  display: flex;
  justify-content: space-between;
  margin-top: 4em;
}
@media (max-width: 767px) {
  .o-footer {
    flex-direction: column-reverse;
  }
}
@media (min-width: 768px) {
  .o-footer {
    margin-top: 0;
  }
}
.o-footer .o-footer__created_by {
  display: flex;
  align-items: center;
  font-family: "robotoregular", sans-serif;
}
@media (max-width: 767px) {
  .o-footer .o-footer__created_by {
    justify-content: center;
    margin-bottom: 3rem;
  }
}
.o-footer .o-footer__logo {
  height: 35px;
  margin-right: 10px;
}
.o-footer .o-footer__twitter {
  background-color: #1DA1F2;
  color: #fff;
  padding: 4px;
  text-decoration: none;
  margin-left: 10px;
}
.o-footer .o-footer__link-list {
  list-style: none;
  padding: 0;
}
@media (max-width: 500px) {
  .o-footer .o-footer__link-list {
    flex-wrap: wrap;
  }
}
@media (max-width: 767px) {
  .o-footer .o-footer__link-list {
    display: flex;
    justify-content: center;
    margin-bottom: 3rem;
  }
}
.o-footer .o-footer__link-elem {
  float: left;
  margin-right: 2rem;
}
@media (max-width: 500px) {
  .o-footer .o-footer__link-elem {
    margin-bottom: 15px;
  }
}
@media (max-width: 767px) {
  .o-footer .o-footer__link-elem {
    float: none;
  }
}
.o-footer .o-footer__link-elem:last-of-type {
  margin-right: 0;
}
.o-footer .o-footer__link {
  color: #1C1C1C;
  text-decoration: none;
  font-size: 1rem;
}
.o-footer .o-footer__link:hover {
  color: #6e6e6e;
}
.o-footer .o-footer__link--big {
  border: 1px #000 solid;
  border-radius: 5px;
  padding: 4px;
}

.o-vertical-menu .o-vertical-menu__link--active {
  font-weight: bolder;
}

.o-menu-list {
  list-style: none;
  padding: 0;
}
.o-menu-list .o-menu-list__li {
  float: left;
  margin-right: 2rem;
}
@media (max-width: 500px) {
  .o-menu-list .o-menu-list__li {
    margin-bottom: 15px;
  }
}
@media (max-width: 767px) {
  .o-menu-list .o-menu-list__li {
    float: none;
  }
}
.o-menu-list .o-menu-list__li:last-of-type {
  margin-right: 0;
}
.o-menu-list .o-menu-list__link {
  color: #1C1C1C;
  text-decoration: none;
  font-size: 1rem;
  background-color: #fff;
}
.o-menu-list .o-menu-list__link:hover {
  color: #6e6e6e;
}
.o-menu-list .o-menu-list__link--big {
  border: 1px #000 solid;
  border-radius: 5px;
  padding: 4px;
}

.o-mail-dialog {
  width: 100%;
  display: flex;
  margin-top: 2em;
  flex-direction: column;
}
@media (min-width: 768px) {
  .o-mail-dialog {
    flex-direction: row;
  }
}
.o-mail-dialog .o-mail-dialog__why {
  list-style: none;
  width: 100%;
  padding: 0;
  margin: 0 0 2em 0;
}
@media (min-width: 768px) {
  .o-mail-dialog .o-mail-dialog__why {
    width: 50%;
    margin: 0;
    padding: 0 20px 0 0;
  }
}
.o-mail-dialog .o-mail-dialog__why-li {
  font-family: "robotoregular", sans-serif;
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  margin-bottom: 1em;
}
@media (min-width: 768px) {
  .o-mail-dialog .o-mail-dialog__why-li {
    margin-bottom: 2em;
  }
}
.o-mail-dialog .o-mail-dialog__why-li:before {
  box-sizing: content-box;
  content: "";
  display: inline-block;
  height: 1.5em;
  width: 1.5em;
  background-image: url("/img/check-icon.svg");
  background-size: contain;
  background-repeat: no-repeat;
  padding-left: 1em;
}
.o-mail-dialog .o-mail-dialog__input-container {
  width: 100%;
}
@media (min-width: 768px) {
  .o-mail-dialog .o-mail-dialog__input-container {
    width: 50%;
    height: 100%;
  }
}

.o-unified-button-input {
  width: 100%;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .o-unified-button-input {
    flex-direction: row;
  }
}
.o-unified-button-input .o-unified-button-input__input {
  font-family: "robotoregular", sans-serif;
  font-size: 1rem;
  box-sizing: border-box;
  border: 1px solid #000;
  width: 100%;
  margin-bottom: 1em;
  padding: 5px;
  outline: none;
}
@media (min-width: 768px) {
  .o-unified-button-input .o-unified-button-input__input {
    margin-bottom: 0;
    width: 60%;
  }
}
.o-unified-button-input .o-unified-button-input__button {
  font-family: "robotoregular", sans-serif;
  font-size: 1rem;
  text-align: center;
  border: none;
  padding: 5px;
  display: block;
  width: 100%;
  background-color: #000;
  color: #fff;
  transition: background-color 0.5s ease;
}
@media (min-width: 768px) {
  .o-unified-button-input .o-unified-button-input__button {
    width: 40%;
  }
}
.o-unified-button-input .o-unified-button-input__button:hover {
  background: #1C1C1C;
}

.o-single-submit-form .o-single-submit-form__input {
  font-family: "robotoregular", sans-serif;
  font-size: 1.3rem;
  border: 2px #000 solid;
  width: 100%;
  margin: 0;
  padding: 5px;
  outline: none;
}
.o-single-submit-form .o-single-submit-form__input:focus {
  box-shadow: 0 0 3px 0 #000;
}
.o-single-submit-form .o-single-submit-form__agree-text {
  margin: 1em 0;
  font-family: "robotoregular", sans-serif;
  color: #6e6e6e;
  font-size: 1rem;
}
.o-single-submit-form .o-single-submit-form__agree-text:last-of-type {
  margin: 1rem 0 2.5rem 0;
}
.o-single-submit-form .o-single-submit-form__agree-links, .o-single-submit-form .o-single-submit-form__agree-links:visited {
  color: #6e6e6e;
}
.o-single-submit-form .o-single-submit-form__button {
  border: none;
  font-family: "robotoregular", sans-serif;
  font-size: 1.3rem;
  text-decoration: none;
  padding: 10px;
  text-align: center;
  display: block;
  width: 100%;
  background-color: #000;
  color: #fff;
  transition: all 0.5s ease;
}
.o-single-submit-form .o-single-submit-form__button:hover {
  transform: scale(1.05);
}

/* * Used whenever something needs to be completely centered
 * */
.o-center-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

/* * Used for App Dashboard
 * */
.o-table {
  width: 100%;
  margin: 20px 0 0 0;
}
.o-table .o-table__th {
  font-family: "robotobolditalic", sans-serif;
  font-size: 1.3rem;
  padding: 5px;
  border: solid #000;
  border-width: 0 1px 1px 0;
  margin: 0;
}
.o-table .o-table__th:last-of-type {
  border-width: 0 0 1px 0;
}
.o-table .o-table__td {
  font-family: "robotoregular", sans-serif;
  font-size: 1.3rem;
  padding: 5px;
  border: solid #000;
  border-width: 0 1px 0 0;
  margin: 0;
}
.o-table .o-table__td:last-of-type {
  border-width: 0;
}

.o-pure-text-wrap {
  white-space: pre-line;
}
.o-pure-text-wrap p {
  font-family: "robotoregular", sans-serif;
  font-size: 1.3rem;
  margin: 0.25em 0;
}
.o-pure-text-wrap ul, .o-pure-text-wrap ol {
  white-space: normal;
  margin: 0;
}

/* * Signify that something is a Component. This is a concrete, implementation-specific piece of UI.
 * * All of the changes you make to its styles should be detectable in the context youâ€™re currently looking at.
 * *
 * * Modifying these styles should be safe and have no side effects.
 * */
/* * Landing Page Components
 * */
.c-landing-header {
  position: relative;
  overflow: hidden;
}
.c-landing-header .c-landing-header__description {
  z-index: 2;
  position: relative;
}
.c-landing-header .c-landing-header__container {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .c-landing-header .c-landing-header__container {
    flex-direction: row;
  }
}
.c-landing-header .c-landing-header__h1-container {
  margin: 0 5%;
}
@media (min-width: 768px) {
  .c-landing-header .c-landing-header__h1-container {
    width: 60%;
  }
}
@media (min-width: 1300px) {
  .c-landing-header .c-landing-header__h1-container {
    width: 40%;
  }
}
@media (min-width: 768px) {
  .c-landing-header .c-landing-header__h1 {
    padding: 0 2rem 0 0;
  }
}
@media (min-width: 992px) {
  .c-landing-header .c-landing-header__h1 {
    padding: 0;
  }
}
@media (min-width: 768px) {
  .c-landing-header .c-landing-header__rating {
    width: 40%;
  }
}
@media (min-width: 1300px) {
  .c-landing-header .c-landing-header__rating {
    width: 60%;
  }
}
.c-landing-header .c-landing-header__signup {
  margin-top: 30px;
}
.c-landing-header .c-landing-header__mockup {
  position: absolute;
  z-index: 0;
  box-shadow: -1px 5px 30px 0px rgba(0, 0, 0, 0.4);
  transform: rotate(-16.8deg);
